export default {
    namespaced: true,
    state: {
        roleDialogActive: false  //是否显示，添加角色弹出框
    },
    mutations: {

        //是否显示，添加角色弹出框
        toogleRoleDialogActive: function (state, isActive) {
            state.roleDialogActive = isActive
        }

    }
}


