  font-weight: normal;
  font-size: 14px;
}
.title::before {
  content: "";
  display: block;
  width: 3px;
  height: 18px;
  background-color: #188ff0;
  position: absolute;
  top: 1px;
  left: 0px;
}
.title.no-mark {
  padding-left: 0px;
}
.title.no-mark::before {
  content: none;
}
.detail-title {
  font-size: 14px;
  color: #000;
  margin-right: 20px;
}
.detail-item {
  font-size: 12px;
  color: #777;
}
.detail-item.red {
  color: #f2082e;
}
.detail-item.green {
  color: #01be6a;
}
.division {
  border-style: solid;
  border-width: 1px;
  border-color: #dfe4e6;
  margin: 0px;
  margin-bottom: 20px;
}
.detail-img {
  display: block;
  border: 0px;
  width: 85%;
  height: 85%;
  margin: 0 auto;
}
.error-message {
  font-size: 14px;
  color: #f2082e;
  margin-bottom: 10px;
}
.error-success {
  font-size: 14px;
  color: #188ff0;
  margin-bottom: 10px;
}
.progress-bg-gray {
  width: 100%;
  height: 10px;
  background: #ebf0f2;
  border-radius: 5px;
  position: relative;
}
.progress-bg-active {
  width: 0%;
  height: 10px;
  background: #188ff0;
  border-radius: 5px;
  position: absolute;
  left: 0px;
  top: 0px;
}
.progress-bg-active.progress-bg-active1 {
  width: 25%;
}
.progress-bg-active.progress-bg-active2 {
  width: 50%;
}
.progress-bg-active.progress-bg-active3 {
  width: 75%;
}
.progress-bg-active.progress-bg-active4 {
  width: 100%;
}
.progress-tips-con {
  width: 100%;
  position: absolute;
  left: 0px;
  top: -40px;
  margin: 0px;
}
.progress-tips-item {
  position: relative;
}
.progress-tips-item::after {
  position: absolute;
  content: "";
  left: 50%;
  margin-left: -15px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background: #ebf0f2;
  color: #666;
  font-size: 14px;
}
.progress-tips-con .progress-tips-item:nth-of-type(1)::after {
  content: "1";
}
.progress-tips-con .progress-tips-item:nth-of-type(2)::after {
  content: "2";
}
.progress-tips-con .progress-tips-item:nth-of-type(3)::after {
  content: "3";
}
.progress-tips-con .progress-tips-item:nth-of-type(4)::after {
  content: "4";
}
.progress-tips-item.active::after {
  background: #188ff0;
  color: #fff;
}
.progress-tips-item .progress-tips-item-text {
  color: #999999;
  text-align: center;
  font-size: 14px;
  padding-bottom: 7px;
}
.progress-tips-item.active .progress-tips-item-text {
  color: #188ff0;
}
.margin-bottom10 {
  margin-bottom: 10px;
}
.receivables-table {
  width: 100%;
  border-color: #e6eced;
  border-style: solid;
  border-collapse: collapse;
}
.receivables-table td {
  padding: 10px 20px;
  font-size: 14px;
  color: #000;
}
.receivables-table td.receivables-table-gray {
  color: #777;
}
.receivables-table tr:nth-of-type(even) {
  background: #f2f6f7;
}
/deep/ .timeTime {
}
.contractBody {
  height: calc(100vh - 340px);
  overflow-x:hidden;
  overflow-y:scroll;
}
.image_box figure {
  width: 100px;
  height: 94px;
  float: left;
  overflow: hidden;
  margin: 0;
  margin-left: 5px;
}
.image_box::after{
  content: '';
  display: block;
  clear: both;
}
/deep/ .image_box img{
  width: 100px;
  height: 60px  ;
}
/deep/ .image_box figcaption{
  text-align: center;
}
</style>



