export default {
  namespaced: true,
  state: {
    partnersDialogActive: false  //是否显示，添加部门弹出框
  },
  mutations: {
    //是否显示，添加部门弹出框
    tooglePartnersDialogActive: function (state, isActive) {
      state.partnersDialogActive = isActive
    }
  }
}



