export default {
    namespaced: true,
    state: {
        staffDialogActive: false  //是否显示，添加员工弹出框
    },
    mutations: {

        //是否显示，添加员工弹出框
        toogleStaffDialogActive: function (state, isActive) {
            state.staffDialogActive = isActive
        }

    }
}


