      },
      /**
       *  获取数据  初始化页面数据
       * @param pageSize 分页每页显示数量
       * @param pageNo    当天分页页码
       * @param searchMsg  模糊搜索信息
       */
      fetchData(pageSize, pageNo) {
        // 清空数据
        this.listResult = {}  // table 总数据
        var _this = this
        this.loading.all = true
        var dataOption = {
          gcid: '0100099',
          userid: 'ab0f10cd02094f7b9b82b82088742557',
          token: 'e40f0b86-2ed6-4b62-8571-e63e1f23bbd3',
          pageNo: pageNo,
          pageSize: pageSize,
          status: 0,
          minPrice: this.searchMsg.minPrice,
          maxPrice: this.searchMsg.maxPrice,
          likeName: this.searchMsg.likeName,
          startDate: this.searchMsg.time[0],
          endDate: this.searchMsg.time[1],

        }
        /* if (searchMsg) {
           dataOption = Object.assign({}, dataOption, searchMsg)
         }*/
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.alreadyCollect,
          data: dataOption
        }).then(function (response) {
           var {data} = response
           if(data.status.code == 200){
             _this.loading.all = false
             _this.screening.areaValue = [] //清空选择
             response.data.result.financeList.forEach((item,index)=>{
               Object.assign(item,{
                 isCheck:false
               })
             })
             _this.listResult = response.data.result;
             _this.totalRecord = response.data.result.totalRecord;
           }
        }).catch(error => {
          _this.loading.all = false;
        });
      },
      /**
       *  计算应收金额
       * @param planPrice 本金
       * @param lateFee   滞纳金
       * @param exemptLateFee  减免滞纳金
       * @param exemptLateFeeStatus 滞纳金减免状态
       * @returns {number}  应收金额
       */
      getMoney(planPrice, lateFee, exemptLateFee, exemptLateFeeStatus) {
        var money = 0;
        if (exemptLateFeeStatus) {
          money = Number(planPrice) + Number(lateFee) - Number(exemptLateFee);
        } else {
          money = Number(planPrice) + Number(lateFee);
        }
        return money
      },

      /**
       *  计算应收金额
       * @param item 当前操作的行数
       */
      allMoney(item) {
        var money = 0;
        if (item.exemptLateFeeStatus) {
          money = Number(item.planPrice) + Number(item.lateFee) - Number(item.exemptLateFee);
        } else {
          money = Number(item.planPrice) + Number(item.lateFee);
        }
        /* var str = ''
         if (item.exemptLateFeeStatus) {
           str = `(公减免滞纳金${item.exemptLateFee}元)`
         }*/
        return money
      },

      /**
       *  收款计划详情 中已收
       *  @param item 当前操作的行数
       */
      receiptMoney(item) {
        var money = 0;
        if (item.status != 5) {
          if (item.exemptLateFeeStatus) {
            money = Number(item.planPrice) + Number(item.lateFee) - Number(item.exemptLateFee);
          } else {
            money = Number(item.planPrice) + Number(item.lateFee);
          }
        }
        return money;
      },
      /**
       * @param item 每条数据
       * @returns {string} 用户状态信息
       */
      collection(){
        this.smsList=[];
        var a = this.listResult;
          var filterList = this.listResult.financeList.filter((item)=>{
            return (item.smsNoticeStauts == true && item.isCheck == true && item.receivedStatus ==1)
          })
          filterList.forEach((item)=>{
            this.smsList.push(item.id)
          })
        if(this.smsList.length>0){
          this.smsNoticeFn(this.smsList)
        }else {
          return
        }

      },
      /**
       *  计算逾期状态 , 改变状态对应的class类名
       * @param item 每条数据
       * @returns {string} 用户状态信息
       */
      computedTime(item) {
        item.num = false
        var str = '';
        var time = Number(item.numDate); //逾期天数
        if (time > 0) {
          item.num = 1
          str += '逾' + time + '天'
        } else if (time == 0) {
          item.num = 2
          str += '已到期'
        } else if (time < 0 && Math.abs(time) < 20) {
          str += Math.abs(time) + '天后'
          item.num = 3
        } else {
          item.num = false
        }
        return str;
      },

      /**
       *  计算用户状态
       * @param item 每条数据
       * @returns {string} 用户状态
       */
      computedState(item) {
        var status = Number(item.status)
        var isRepay = Number(item.isRepay)
        var lateFee = Number(item.lateFee)
        var receivedStatus = item.receivedStatus //能否进行短信催收 1可以0 不可以
        item.classNum = false
        var str = ''

        if (isRepay == 1 && status == 0) {
          str = '待确认'
          item.classNum = 1
          item.sectionOption = ['确认收款', '驳回']
          item.placeholder = item.sectionOption[0]
        } else if (isRepay == 1 && status == 2) { // 应收驳回
          item.classNum = 3
          item.sectionOption = ['重新收款']
          item.placeholder = item.sectionOption[0]
          str = '已驳回'
        } else if (isRepay == 1 && status == 3) {  //已收中驳回
          item.classNum = 4
          item.sectionOption = ['确认收款', '驳回']
          item.placeholder = item.sectionOption[0]
          str = '被驳回'
        } else if (isRepay == 0 && status == 5) {
          item.classNum = 2
          // item.sectionOption = ['短信催收', '减免滞纳金', '恢复滞纳金']
          if (lateFee > 0) {
            var btn1 = this.isButtonAuthor('0a620fa7592849828346d8aeaab1e1a3') //是否有短信催收权限
            var btn2 = this.isButtonAuthor('5cb6a765fc4841eca89902e2baf6e273') //是否有减免滞纳金权限
            var btn3 = this.isButtonAuthor('443ffadbfccc47f29580c9787b918e2d') //是否有恢复滞纳金权限
            if(btn1&&btn2&&btn3&&receivedStatus==1){
              item.sectionOption = ['短信催收', '减免滞纳金', '恢复滞纳金']
            }else if(btn1&&btn2&&btn3&&receivedStatus==0){
              item.sectionOption = ['减免滞纳金', '恢复滞纳金']
            }else if(btn1==false&&btn2&&btn3){
              item.sectionOption = ['减免滞纳金', '恢复滞纳金']
            }else if(btn1&&btn2==false&&btn3&&receivedStatus==1){
              item.sectionOption = ['短信催收', '恢复滞纳金']
            }else if(btn1&&btn2==false&&btn3&&receivedStatus==0){
              item.sectionOption = ['恢复滞纳金']
            }else if(btn1&&btn2&&btn3 ==false&&receivedStatus==1){
              item.sectionOption = ['短信催收', '减免滞纳金']
            }else if(btn1&&btn2&&btn3 ==false&&receivedStatus==0){
              item.sectionOption = [ '减免滞纳金']
            }else if(btn1&&btn2 ==false&&btn3==false&&receivedStatus==1){
              item.sectionOption = ['短信催收']
            }else if(btn1&&btn2 ==false&&btn3==false&&receivedStatus==0){
              item.sectionOption = ['查看']
            }else if(btn1==false&&btn2&&btn3==false){
              item.sectionOption = ['减免滞纳金']
            }else if(btn1==false&&btn2==false&&btn3){
              item.sectionOption = ['恢复滞纳金']
            }else if(btn1==false&&btn2==false&&btn3==false){
              item.sectionOption = ['查看']
            }

            item.smsNoticeStauts = true
            if(item.sectionOption.length>0){
              item.placeholder = item.sectionOption[0]
            }else {
              item.placeholder=''
            }
          } else {
            var btn1 = this.isButtonAuthor('0a620fa7592849828346d8aeaab1e1a3') //是否有短信催收权限
            if(btn1&&receivedStatus ==1){
              item.sectionOption = ['短信催收']
              item.smsNoticeStauts = true
            }else {
              item.sectionOption = ['查看']
              item.smsNoticeStauts = false
            }


            if(item.sectionOption.length>0){
              item.placeholder = item.sectionOption[0]
            }else {
              item.placeholder=''
            }
          }
          str = '待收款'
        }
        return str;
      },

      /**
       * 改变每页的分页数量
       * @param val 每页分页的数量
       */
      handleSizeChange(val) {
        this.pageSize = val
        this.fetchData(val, this.currentPage)
