      handleCurrentChange(val) {
        this.checkedAll = ''
        this.currentPage = val
        this.fetchData(this.pageSize, val)
      },

      /**
       * 显示收支详情
       * @param row 点击的当前行数据
       */
      showDetail(row) {
        this.currentItem  = row
        var status = Number(row.status)
        var isRepay = Number(row.isRepay)
        if (isRepay == 1 && status == 0) { //待确认
          this.sureStatus = '待确认'
          this.paymentDetailBtn2 = true
          this.sureStatusMsg = '确认收款'
        } else if (isRepay == 1 && status == 2) { // 应收驳回 已驳回
          this.sureStatus = '已驳回'
          this.sureStatusMsg = '确认收款'
          this.paymentDetailBtn2 = true
        } else if (isRepay == 1 && status == 3) {  //已收中驳回  被驳回
          this.sureStatus = '被驳回'
          this.sureStatusMsg = '确认收款'
          this.paymentDetailBtn2 = true
        } else if (isRepay == 0 && status == 5) { //待收款
          this.sureStatus = '待收款'
          this.paymentDetailBtn = true
        }
        this.confirmReceipt()

        var {id, contractId} = row
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.feeDetails,
          data: {
            id: id,
            contractId: contractId
          }
        }).then(function (response) {
          const {data} =response
          if(data.status.code == 200){
            _this.viewDetailData = response.data.result
          }
        })
      },

      /**
       * 模糊搜索
       */
      search() {
        this.fetchData(this.pageSize, this.currentPage)
      },

      /**
       * 获取详细还款数据 (带时间线)
       * @param item当前行数据
       */
      showContractPlan(item) {
        var {gcid, contractId} = item
        this.ContractPlanBtn = true
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReriods,
          data: {
            gcid,
            contractId
          }
        }).then(function (response) {
          var {data} = response
          if (data.status.code == 200) {
            _this.ContractPlanMsg = response.data.result
          }
        }).catch(error => {
          this.ContractPlanBtn = false
        })
      },

      /**
       * 用户还款状态
       * @param item 当前行数据
       */
      stateMsg(item) {
        var num = '';
        var str = '';
        var classNum = ''
        var obj = {}
        num = Number(item.status)
        if (num == 3 || num == 0) {
          str = '待确认'
          classNum = 3
        } else if (num == 1) {
          str = '已还款'
          classNum = 1
        } else if (num == 2) {
          str = '还款失败'
          classNum = 2
        } else {
          str = '待收款'
          classNum = 4
        }
        return {
          str,
          classNum
        };
      },

      /**
       * 阻止表格中的下拉选择进行事件冒泡
       */

      /**
       * 操作中的下拉选择
       * @param item 当前行数据
       */
      handleDo(item) {
        const { selectedIndex} = this.listResult.financeList;
        // 清除弹窗数据
        this.currentItem.exemptLateFee = ''
        this.currentItem.exemptLateFeeRemark = '';
        this.currentItem = item.row;
        this.currentItemIndex =item.$index;
        var val = item.row.selectedIndex;
        if (val == '通过') {
          this.confirmReceiptInputBtn = true
        } else if (val == '驳回') {
          this.paymentRejectBtn = true
        } else if (val == '确认收款' || val == '重新收款') {
          this.confirmReceiptBtn = true
          this.confirmReceipt()
        } else if (val == '减免滞纳金' || val == '恢复滞纳金') {
          this.breaksMoneyBtn = true
        } else if (val == '短信催收') {
          this.smsNotice()
        }else if(val =='查看'){
          this.paymentDetailBtn = true
          this.showDetail(this.currentItem)
        }
      },
      smsNotice() {
        var ids = [];
        this.paymentDetailBtn = false
        var id = this.currentItem.id;
        ids.push(id)
        this.smsNoticeFn(ids)
      },
      smsNoticeFn(ids){
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.smsNotice,
          data: {
            ids:ids
          }
        }).then((response) => {
          const {data} = response;
          if (data.status.code == 200) {
            _this.$message({
              message: '操作成功',
              type: 'success',
              duration: 1000
            });
           // _this.reload()
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },
      /**
       * 控制通过弹窗 表单提交函数
       */
      passDialog(val) {
        if (val == 1) {
          this.paymentRejectBtn = true
        } else {
          this.confirmReceiptInputBtn = true;
        }
        this.confirmReceiptBtn = false
      },

      /**
       * 提交通表单  表单提交函数
       */
      pass() {
        const self = this;
        const userid = tool.getUserId();
        if (!userid) {
          self.$message.error("登录超时，请先登录！");
          return;
        }
        var _this = this
        var remark = this.confirmReceiptForm.msg;
        var confirmTime = this.confirmReceiptForm.date
        var {id, gcid} = this.confirmReceiptFormDialog.financeBill
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeAffirmPrice,
          data: {
            id,
            gcid,
            remark,
            userid,
            confirmTime
          }
        }).then((response) => {
          const {data} = response;
          if (data.status.code == 200) {
            _this.loading.sure = false;
            _this.paymentRejectBtn = false
            _this.confirmReceiptInputBtn = false
            _this.confirmReceiptForm.msg =''
            _this.$message({
              message: '操作成功',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          }
        }).catch(error => {
          _this.loading.sure = false;
        })
      },

      /**
       * 驳回弹窗  表单提交函数
       */
      reject() {
        var _this = this;
        this.paymentRejectBtn = false;
        this.loading.back = true;
        var {id, gcid} = this.currentItem;
        var rejectReason = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReject2,
          data: {
            id,
            gcid,
            rejectReason
          }
        }).then((response) => {
          _this.loading.back = false;
          const {data} = response;
          if (data.status.code == 200) {
            this.paymentRejectBtn = false
            _this.$message({
              message: '操作成功',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          } else {
            _this.$message({
              showClose: true,
              message: data.status.msg,
              type: 'error',
              duration: 1000
            });
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },

      /**
       * 驳回弹窗
       */
      reject2() {
        var _this = this;
        this.paymentRejectBtn = false;
        this.loading.back = true;
        var {id, gcid} = this.currentItem;
        var rejectReason = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReject2,
          data: {
            id,
            gcid,
            rejectReason
          }
        }).then((response) => {
          _this.loading.back = false;
          const {data} = response;
          if (data.status.code == 200) {
            this.paymentRejectBtn = false
            _this.$message({
              message: '操作成功',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          } else {
            _this.$message({
              showClose: true,
              message: data.status.msg,
              type: 'error',
              duration: 1000
            });
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },

      /**
       * 确认收款弹框  表单提交函数(产生了滞纳金)
       */
      confirmReceipt() {
        var _this = this
        var {id, contractId} = this.currentItem;
        var msg = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.affirmMessage,
          data: {
            id,
            contractId
          }
        }).then((response) => {
          var {data} = response
          if (data.status.code == 200) {
            data.result.pictrues.forEach((item,index)=>{
              Object.assign(item,{
                w: 800,
                h: 600
              })
            });
            _this.confirmReceiptFormDialog = data.result
          }
        })
      },
      //刷新数据
      reload() {
        var currentPage = 1
        this.fetchData(this.pageSize, currentPage);
      },
      /**
       * 恢复和减免滞纳金 表单提交函数
       */
      revertMoney(val) {
        var _this = this
        var {id, gcid} = this.currentItem;
        var exemptLateFeeStatus = val
        var exemptLateFee = this.currentItem.exemptLateFee; //输入可减免滞纳金
        var lateFee = this.currentItem.lateFee  // 滞纳金
        // 滞纳金数据进行校验
        if (Number(lateFee) < Number(exemptLateFee)) {
          this.$message.error('减免滞纳金金额有误')
          return
        }
        var exemptLateFeeRemark = this.currentItem.exemptLateFeeRemark

        var dataOption = {
          id,
          gcid,
          exemptLateFee,
          exemptLateFeeRemark,
          exemptLateFeeStatus
        }
        /*this.loading.breaks = true*/
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.exemptLateFee,
          data: dataOption
        }).then((response) => {
          _this.loading.breaks = false;
          const {data} = response;
          if (data.status.code == 200) {
            // 清除弹窗数据
            _this.currentItem.exemptLateFee = ''
            _this.currentItem.exemptLateFeeRemark = ''
            _this.$alert("操作成功", "提示", {
              callback: action => {
                _this.fetchData(_this.pageSize, _this.currentPage);
                _this.breaksMoneyBtn = false
              }
            });
          }
        }).catch(error => {
          _this.loading.breaks = false;

        });
      },
      /**
       * 减免滞纳金弹窗 表单提交函数
       */
      breaksMoney() {

      },
      //阻止事件冒泡
      stopPor() {
      },
      handleSelectionChange(val) {
        var {id} = val[0]
        this.selectList.push(id)
      }
    }
  };
</script>

