    text-align: center;
  }

  /*必填信息*/
  .require {
    position: relative;
  }

  .require::before {
    position: absolute;
    content: '*';
    color: #ff2020;
    top: -7px;
    left: -6px;
    font-size: 14px;
  }

  .confirmReceipt .allMoney {
    font-size: 20px;
  }

  .confirmReceipt .moneyBox {
    font-size: 14px;
  }

  .ContractPlan .state_content.blue {
    color: #24b0fc;
  }

  .ContractPlan .state_content.yellow {
    color: #f9d48c;
  }

  .ContractPlan .state_content.green {
    color: #82daae;
  }

  .ContractPlan .state_content.red {
    color: #f88797;
  }

  .searchPagination {
    position: fixed;
    bottom: 20px;
    width: 89%;
    background: #f9f9f9;
    padding: 10px 20px 10px 10px;
    box-sizing: border-box;
    border: 1px solid #e5e5e5;
  }

  .tableDate {
    background: #fff;
  }
  .imgBox img{
    width: 100px;
    height: 100px;
  }
 .contractNo{
   border-bottom: 1px solid #00b7ee;
   cursor: pointer;
 }
  /deep/ .el-table__body-wrapper{
    height: calc(100vh - 310px);
  }
  .el-main{
    padding: 20px 20px 0 20px;
  }
  .collection {
    width: 68px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border-radius: 4px;
    color: #f7fbfe;
    background: #2591ed;
    display: inline-block;
    margin-left: 10px;
    cursor: pointer;
  }
  .image_box figure {
    width: 100px;
    height: 94px;
    float: left;
    overflow: hidden;
    margin: 0;
    margin-left: 5px;
  }
  .image_box::after{
    content: '';
    display: block;
    clear: both;
  }
  /deep/ .image_box img{
    width: 100px;
    height: 60px;
  }
  /deep/ .image_box figcaption{
    text-align: center;
  }
</style>



